import React from 'react';
import Helmet from 'react-helmet';
import '../../../Components/Pages/Tutorials.css';
import { BlockMath } from 'react-katex';
import Inline from '../../../Components/Latex/Inline';
import eqref from '../../../Components/Latex/eqref';
import newcommand from '../../../Components/Latex/newcommand';
import '../../../../node_modules/katex/dist/katex.css';
import Image from '../../../Components/Image/Image';
import Disqus from 'disqus-react';
const RiccatiEq = (props) => {
const disqusShortname = 'marofe-github-io';
const disqusConfig = {
        url: 'https://marofe.github.io/?p='+props.note.link,
        identifier: 'note-'+props.note.link,
        title: props.title
    };
return <article>
      <Helmet>
        <title>{props.note.title} | Marofe</title>
        <meta name="description" content={props.note.desc} />
    </Helmet>
    <h1>{props.title}</h1>
<p>{props.desc}</p>
    <p align="right">Last Update:  25 Fev, 2020.</p>
<div>
<h2>The Differential Riccati Equation</h2>

The selection of optimal control law and the design of optimal filters require the solving of a Riccati Equation. So, Riccati type equations emerge naturally on control and filtering problems of  dynamic systems. 

To find solutions for Riccati Equations, it is convenient to make subdivisions based on the nature of the coefficient matrices and the time interval considered. 
<ul>
<li>
	Time-varying coefficients; <Inline math="t_1<\infty"/> 
	</li>
	<li>
	Time-varying coefficients; <Inline math="t_1\rightarrow \infty"/> 
	</li>
	<li>
	Constant coefficients; <Inline math="t_1<\infty"/> 
	</li><li>
	Constant coefficient; <Inline math="t_1\rightarrow \infty"/> 
	</li>
	</ul>

In particular, we restrict attention to the Riccati equation occurring to a situation when a boundary condition will be given at <Inline math="t_1"/> with the solution desired for <Inline math="t\le t_1"/>.  Recall that the Riccati Equation in the context of the control problem runs "backward" in time.

<Image src="/images/p_finite.svg" width="50%"/>

The following results are based on the book <b><a href="#Anderson1971">[Anderson1971]</a></b>. It shows that in all four cases, it is possible to replace the problem of solving the <i>nonlinear</i> Riccati differential equation by the problem of solving a <i>linear</i> differential equation and then computing a matrix inverse. 
<h3>Time-Varying Coeff. and Finite Horizon</h3>

Consider the differential Riccati equation for <Inline math="t \le t_1"/> of the form
<BlockMath math="
\begin{aligned}
	-\dot{P}=P(t)F(t)+F(t)^\intercal P(t)-P(t)G(t)R^{-1}(t)G(t)^\intercal P(t)+Q(t)
\end{aligned}"/>
and the following assumptions: 
<BlockMath math="\begin{aligned}
P(t_1)&=P_1=P_1^\intercal \ge 0,\\
Q(t)&=Q(t)^\intercal \ge 0,\\
R(t)&=R(t)^\intercal >0.
\end{aligned}"/>

Associated with the <i>Nonlinear Differential Riccati Equation</i> we define an augmented <i>linear</i> differential equation
<BlockMath math="\begin{aligned}
	\left[
	\begin{matrix}
	\dot{X}(t)\\
	\dot{Y}(t)
	\end{matrix}
	\right]=\left[
	\begin{matrix}
	F(t) & -G(t)R^{-1}(t)G(t)^\intercal \\
	-Q(t) & -F(t)^\intercal
	\end{matrix}
	\right]\left[
	\begin{matrix}
	X(t)\\
	Y(t)
	\end{matrix}
	\right],\quad \left[
	\begin{matrix}
	X(t_1)\\
	Y(t_1)
	\end{matrix}
	\right]=\left[
	\begin{matrix}
	I\\
	P_1
	\end{matrix}
	\right].
\end{aligned}"/>
<div className="lemma">
	<b>Lemma:</b><br/>
	If the solution of the Riccati Equation exists on <Inline math="[t,t_1]"/> then the solution of the linear counter-part has the property that <Inline math="X^{-1}(t)"/> also exists and
<BlockMath math="	\begin{aligned}
		P(t)=Y(t)X^{-1}(t).
\end{aligned}"/>
</div>
<div className="proof">
	<b>Proof:</b><br/>
	By the <i>product rule</i> and <i>inverse rule</i> of matrix calculus, one has
	<BlockMath math="	\begin{aligned}
\frac{d}{dt}[Y(t)X^{-1}(t)]&=\dot{Y}(t)X^{-1}(t)-Y(t)X^{-1}(t)\dot{X}(t)X^{-1}(t).
\end{aligned}"/>
	
	Notice that from the augmented ODE we have
<BlockMath math="	\begin{aligned}
		\dot{X}(t)&=F(t)X(t)-G(t)R^{-1}(t)G(t)^\intercal Y(t),\\
		\dot{Y}(t)&=-Q(t)X(t)-F(t)^\intercal Y(t)
\end{aligned}"/>
	Thus,
	<BlockMath math="	\begin{aligned}
\frac{d}{dt}[YX^{-1}]&=(-QX-F^\intercal Y)X^{-1}-YX^{-1}(FX-GR^{-1}G^\intercal Y)X^{-1}\\
&=-QXX^{-1}-F^\intercal YX^{-1}-YX^{-1}FXX^{-1}+YX^{-1}GR^{-1}G^\intercal YX^{-1}\\
&=-Q-F^\intercal (YX^{-1})-(YX^{-1})F+(YX^{-1})GR^{-1}G^\intercal(YX^{-1})\\
&=-Q-F^\intercal P-PF+PGR^{-1}G^\intercal P
\end{aligned}"/>
		
	where for notation simplicity we adopt <Inline math="X(t)=X,Y(t)=Y,Q(t)=Q,F(t)=F,G(t)=G,R(t)=R"/> and <Inline math="P(t)=P"/>.  As result, we conclude that
	<BlockMath math="	\begin{aligned}
-\dot{P}=Q+F^\intercal P+PF-PGR^{-1}G^\intercal P.
\end{aligned}"/>
The preceding manipulations also shows that if <Inline math="X^{-1}(\sigma)"/> exists for all <Inline math="\sigma \in [t,t_1]"/> then <Inline math="P(t)"/> exists for the same interval. Let us now check that the existence of <Inline math="P(t)"/> guarantees the existence of <Inline math="X^{-1}(t)"/>.
<p/>
Let <Inline math="\Phi(\cdot,\cdot)"/> be the <i>Transition Matrix</i> associated with the system
<BlockMath math="\begin{aligned}
	\dot{X}=[F(t)-G(t)R^{-1}(t)G(t)^\intercal P(t)]X(t).
\end{aligned}"/>
As long as <Inline math="P(t)"/> exists for <Inline math="t\le t_1"/> then <Inline math="\Phi(\cdot,\cdot)"/> is defined for all its arguments values less or equal to <Inline math="t_1"/>.  We claim that
<BlockMath math="\begin{aligned}
	X(t)=\Phi(t,t_1),\quad Y(t)=P(t)\Phi(t,t_1).
\end{aligned}"/>
This is sufficient to prove that <Inline math="X^{-1}(t)"/> exists since <Inline math="\Phi(t,t_1)^{-1}=\Phi(t_1,t)"/> is known to exist.

To verify the claim, we have
<BlockMath math="\begin{aligned}
\begin{aligned}
	\dot{\Phi}&=[F-GR^{-1}G^\intercal P]\Phi\\
	&=FX-GR^{-1}G^\intercal Y=\dot{X}
\end{aligned}
\end{aligned}"/>
and
<BlockMath math="\begin{aligned}
	\begin{aligned}
	\frac{d}{dt}[P\Phi]&=\dot{P}\Phi+P\dot{\Phi}\\
&=-(PF+F^\intercal P-PGR^{-1}G^\intercal P+Q)\Phi +P(F\Phi-GR^{-1}G^\intercal Y)\\
&=-QX -F^\intercal Y=\dot{Y}
	\end{aligned}
\end{aligned}"/>
Therefore, the relations <Inline math="X(t)=\Phi(t,t_1),\quad Y(t)=P(t)\Phi(t,t_1)"/> satisfy the augmented linear ODE.
<p align="right"><Inline math="\blacksquare"/></p>
</div>

<div className="remark">
	<b>Remark: </b>
Another scenario that might occur is when <Inline math="t_1\rightarrow \infty"/> and the matrix coefficients are varying. In this case, an approximation for <Inline math="P_\infty"/> is possible to obtain by integration of the linear ODE. However, that approximation depends on the length <Inline math="t_1"/>  Thus, the approximation becomes better as <Inline math="t_1"/> is increased.
</div>
<h3>Const. Coeff and Finite Horizon (<Inline math="t_1<\infty"/>)</h3>

For this scenario, consider
<BlockMath math="\begin{aligned}
-\dot{P}=P(t)F+F^\intercal P(t)-P(t)GR^{-1}G^\intercal P(t)+Q
\end{aligned}"/>
where <Inline math="F,G,R"/> and <Inline math="Q"/> are constant matrices. The respective augmented linear differential equation is
<BlockMath math="\begin{aligned}
\left[
\begin{matrix}
\dot{X}(t)\\
\dot{Y}(t)
\end{matrix}
\right]=\left[
\begin{matrix}
F & -GR^{-1}G^\intercal \\
-Q & -F^\intercal
\end{matrix}
\right]\left[
\begin{matrix}
X(t)\\
Y(t)
\end{matrix}
\right],\quad \left[
\begin{matrix}
X(t_1)\\
Y(t_1)
\end{matrix}
\right]=\left[
\begin{matrix}
I\\
P_1
\end{matrix}
\right].
\end{aligned}"/>
As long as all coefficients are constant, the closed-form solution is
<BlockMath math="\begin{aligned}
\left[
\begin{matrix}
X(t)\\
Y(t)
\end{matrix}
\right]=\exp\left(\left[
\begin{matrix}
F & -GR^{-1}G^\intercal \\
-Q & -F^\intercal
\end{matrix}
\right](t_1-t)\right)\left[
\begin{matrix}
X(t_1)\\
Y(t_1)
\end{matrix}
\right].
\end{aligned}"/>
Setting,
<BlockMath math="\begin{aligned}
\left[ 
\begin{matrix}
\Phi_{11}(t) && \Phi_{12}(t)\\
\Phi_{21}(t) && \Phi_{22}(t)
\end{matrix}
\right]=
\exp\left(\left[
\begin{matrix}
F & -GR^{-1}G^\intercal \\
-Q & -F^\intercal
\end{matrix}
\right](t_1-t)\right)
\end{aligned}"/>
then, the Riccati closed-form solution is
<BlockMath math="\begin{aligned}
P(t)=\left[\Phi_{21}(t)X(t_1)+\Phi_{22}(t)Y(t_1)\right]\left[\Phi_{11}(t)X(t_1)+\Phi_{12}(t)Y(t_1)\right]^{-1}.
\end{aligned}"/>
<h3>Constant Coeff. and <Inline math="t_1\rightarrow \infty"/></h3>

When <Inline math="t_1 \rightarrow \infty"/> with constant coefficients we obtain the so-called  <i>Algebraic Riccati Equation</i>
<BlockMath math="\begin{aligned}
P_\infty F+F^\intercal P_\infty-P_\infty GR^{-1}G^\intercal P_\infty+Q=0.
\end{aligned}"/>
<Image src="/images/p_infinite.svg" width="50%"/>
This is also known as the <i>Steady-state Solution</i> of the Riccati Equation. It is implicitly assumed that the dynamic system associated with the Riccati equation is controllable and observable. The controllability assumption guaranteeing that the steady-state solution exists and the observability assumption assures that this solution is positive definite.
<p/>
There are plenty of different approaches in the literature to solve the Algebraic version of Riccati Equation. Here, we focus on only one method that consists in construct the augmented matrix
<BlockMath math="\begin{aligned}
	M=\left[
	\begin{matrix}
	F & -GR^{-1}G^\intercal \\
	-Q & -F^\intercal 
	\end{matrix}
	\right].
\end{aligned}"/>

This <Inline math="M"/> matrix has the property that there is no pure imaginary eigenvalue and if <Inline math="\lambda"/> is an eigenvalue  of <Inline math="M"/>  so is <Inline math="-\lambda"/>.  We then construct a matrix <Inline math="T"/>
<BlockMath math="\begin{aligned}
T=\left[
\begin{matrix}
T_{11} & T_{12} \\
T_{21} & T_{22} 
\end{matrix}
\right]
\end{aligned}"/>
such that
<BlockMath math="\begin{aligned}
	T^{-1}MT=\left[
	\begin{matrix}
	-\Lambda & 0 \\
	0 & \Lambda 
	\end{matrix}
	\right].
\end{aligned}"/>
Thus, the solution is simply given by
<BlockMath math="\begin{aligned}
	P_\infty=T_{21}T_{11}^{-1}.
\end{aligned}"/>
<div className="proof">
	<b>Proof:</b> <br/>
To check this solution, first notice that
<BlockMath math="\begin{aligned}
	MT=T\left[
	\begin{matrix}
	-\Lambda & 0 \\
	0 & \Lambda 
	\end{matrix}
	\right].
\end{aligned}"/>
This results in
<BlockMath math="\begin{aligned}
	FT_{11}-GR^{-1}G^\intercal T_{21}&=-T_{11}\Lambda,\\
	-QT_{11}-F^\intercal T_{21}&=-T_{21}\Lambda.
\end{aligned}"/>

Multiplying by <Inline math="T_{11}^{-1}"/> on the right side of both equations and by <Inline math="T_{21}T_{11}^{-1}"/> only in the first, we have
<BlockMath math="\begin{aligned}
	T_{21}T_{11}^{-1}F-T_{21}T_{11}^{-1}GR^{-1}G^\intercal T_{21}T_{11}^{-1}=-T_{21}\Lambda T_{11}^{-1}
\end{aligned}"/>
and
<BlockMath math="\begin{aligned}
	-Q-F^\intercal T_{21}T_{11}^{-1}=-T_{21}\Lambda T_{11}^{-1}.
\end{aligned}"/>
Therefore,
<BlockMath math="\begin{aligned}
	(T_{21}T_{11}^{-1})F+F^{\intercal}(T_{21}T_{11}^{-1})-(T_{21}T_{11}^{-1})GR^{-1}G^\intercal (T_{21}T_{11}^{-1})+Q=0.
\end{aligned}"/>
<p align="right"><Inline math="\blacksquare"/></p>
</div>
<h3><Inline math="P(t)"/> based on <Inline math="P_\infty"/></h3>

As discussed in <b><a href="#Anderson1971">[Anderson1971][pg. 361]</a></b>, if the steady-state solution is available in advance, we can establish a closed-form solution for the differential Riccati Equation for the entire horizon. In other words, we seek the expression <Inline math="P(t)"/> for 
<BlockMath math="\begin{aligned}
	-\dot{P}=PF+F^\intercal P-PGR^{-1}G^\intercal P +Q,
\end{aligned}"/>
given <Inline math="P_\infty"/> such that
<BlockMath math="\begin{aligned}
	P_\infty F+F^\intercal P_\infty-P_\infty GR^{-1}G^\intercal P_\infty +Q=0.
\end{aligned}"/>

For this purpose, consider
<BlockMath math="\begin{aligned}
	P(t)=P_\infty+Z^{-1}(t),\quad  t\le 0
\end{aligned}"/>
where <Inline math="Z(t)"/> is the solution of 
<BlockMath math="\begin{aligned}
\dot{Z}(t)=[A-GR^{-1}G^\intercal P_\infty]Z(t)+Z(t)[A-GR^{-1}G^\intercal P_\infty]^\intercal-GR^{-1}G^\intercal
\end{aligned}"/>
which is a <i>Differential Lyapunov Equation</i>. The solution for <Inline math="Z(t)"/> is
<BlockMath math="\begin{aligned}
	Z(t)=\bar{Z}+e^{-\tilde{A}t}[Z(0)-\bar{Z}]e^{-\tilde{A}^\intercal t}
\end{aligned}"/>
where
<BlockMath math="\begin{aligned}
	\tilde{A}&=A-GR^{-1}G^\intercal P_\infty\\
	0&=\tilde{A}\bar{Z}+\bar{Z}\tilde{A}-GR^{-1}G^\intercal\\
	Z(0)&=[P(0)-P_\infty]^{-1}
\end{aligned}"/>
Thus, we can write the close-form solution for the Differential Riccati Equation as
<BlockMath math="\begin{aligned}
	P(t)=P_\infty+[\bar{Z}+e^{-\tilde{A}t}[Z(0)-\bar{Z}]e^{-\tilde{A}^\intercal t}]^{-1},\quad t\le 0
\end{aligned}"/>


<h2>Analytical Solution of the Differential Lyapunov Equation</h2>

Consider the differential Lyapunov equation of the form
<BlockMath math="\begin{aligned}
\dot{X}(t)=A^\intercal X(t)+X(t)A+Q,\quad X(0)=X_0.
\end{aligned}"/>

<div className="lemma">
	<b>Lemma:</b><br/>

	The analytical closed-form solution for <i>Differential Lyapunov Equation</i> is given by
<BlockMath math="	\begin{aligned}
	X(t)=\bar{X}+e^{At}(X(0)-\bar{X})e^{A^\intercal t}.
\end{aligned}"/>
	where <Inline math="\bar{X}"/> is solution of the <i>Algebraic Lyapunov Equation</i>
<BlockMath math="	\begin{aligned}
	A\bar{X}+\bar{X}A^\intercal -Q=0.
\end{aligned}"/>
</div>
<div className="proof">
	<b>Proof:</b><br/>
	First, one can proof that if A is Hurwitz then the algebraic equation <eqref eqref="{eq-algebraic_lyapunov_negative}"/> has unique solution. Thus, we can rewrite the Diff. Lyapunov Equation as
<BlockMath math="	\begin{aligned}
	\frac{d}{dt}({X(t)-\bar{X}})=A(X(t)-\bar{X})+(X(t)-\bar{X})A^\intercal
\end{aligned}"/>
	therefore, 
	<BlockMath math="	\begin{aligned}
	X(t)=\bar{X}+e^{At}(X(0)-\bar{X})e^{A^\intercal t}.
\end{aligned}"/>
<p align="right"><Inline math="\blacksquare"/></p>
</div>
<div className="remark">
	<b>Remark: </b>
	The solution of the <i>Algebraic Lyapunov Equation</i> can be obtained employing the Kronecker Product
<BlockMath math="	\begin{aligned}
	vec(\bar{X})=\left[A\otimes A\right]^{-1}vec(Q).
\end{aligned}"/>
</div>
<h3>References:</h3>
<p><b><a href="#" name="Anderson1971"></a>[Anderson1971]</b> B. D. O. Anderson and J. B. Moore, Linear optimal control. 1971.</p>
</div>
<p/>
<Disqus.DiscussionEmbed shortname={disqusShortname} config={disqusConfig} />
</article>
}
export default RiccatiEq;